* {
    margin: 0;
    padding: 0;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
}

ol,
ul,
li {
    list-style: none;
}

a {
    text-decoration: none;
    color: #333;
}


.navbar {
    width: 100%;
    background-color: #00437b;
    position: fixed;
    top: 0;
    z-index: 2;

    .navbar-nav {

        .nav-link {
            color: #fff;
            font-weight: 600;

            &:hover {
                color: #afd32f;
            }

            .active {
                color: #afd32f;
            }

            .icon_font {
                font-size: 18px;
                padding: 0 5px;
            }

        }
    }

    .container {


        >a {
            color: #fff;
            font-weight: 900;

            &.navbar-brand {
                font-size: 22px;
                line-height: 10px;
            }
        }

        .navbar-toggler {
            background-color: #fff;
            padding: 1px 5px;
        
        }

        .navbar-collapse {

            .nav-col {
                flex-direction: row;

                a {
                    padding-right: 10px;
                }
            }
        }
    }
}

.slide {
    .carousel-inner {

        .carousel-item {

            .carousel-caption {
                margin-bottom: 50px;
            }

            img {
                width: 100%;
                height: 100%;
            }
        }
    }
}

// 상품목록
.product_wrap {
    margin-top: 80px;

    >h4 {
        text-align: center;
        font-weight: 800;
        font-size: 2rem;
        padding-bottom: 20px;
        color: #333;
    }

    .row {

        >div {

            margin: 0 0 70px;

            h4 {
                font-size: 0.95rem;
                line-height: 2;
                cursor: default;
            }

            span {
                cursor: default;
            }

            img {
                cursor: pointer;

                &:hover {
                    opacity: 0.8;
                }
            }

            .product_bottom {
                display: flex;
                align-items: center;
                justify-content: space-between;

                span {
                    font-size: 0.95rem;
                }

                .heart {
                    display: flex;
                    align-items: center;

                    img {
                        cursor: pointer;
                    }

                    span {
                        padding-left: 5px;
                        font-size: 12px;
                    }
                }

            }

        }
    }
}

.more_btn {
    text-align: center;
    margin: 0 0 20px;

    button {
        border-radius: 0px;
        font-size: 0.85rem;
        font-weight: 500;
    }
}

.view_wrap {
    margin: 80px auto;

    .view_left {
        position: relative;
        text-align: center;

        .view_text {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: default;

            h1 {
                font-weight: 600;
                color: #fff;
                letter-spacing: 3px;
            }

            div {
                display: inline-block;
                border: 1px solid #fff;
                padding: 10px 30px;
                color: #fff;
                margin-top: 40px;
                cursor: pointer;

                &:hover {
                    border: 1px solid #00437b;
                    color: #00437b;
                }
            }
        }
    }

    .view_right {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        >span {
            position: absolute;
            top: 35%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: #fff;
            border: 1px solid #Fff;
            padding: 10px 30px;
            cursor: pointer;

            &:hover {
                border: 1px solid #00437b;
                color: #00437b;
            }
        }

        .view_bottom {
            img {
                cursor: pointer;

                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
}

.video_wrap {
    margin: 80px auto;

    h4 {
        text-align: center;
        font-weight: 800;
        font-size: 2rem;
        padding-bottom: 20px;
        color: #333;
    }
}

.product_sort_wrap {
    margin: 80px auto;

    .sort_btn {
        text-align: right;
        margin-bottom: 10px;

        h4 {
            text-align: center;
            font-weight: 800;
            font-size: 2rem;
            padding-bottom: 20px;
            color: #333;
        }

        button {
            margin-left: 10px;
            font-size: 14px;
            border-radius: 0;
            padding: 3px 10px;
        }
    }
}


.news_wrap {
    width: 90%;
    margin: 80px auto;

    h4 {
        text-align: center;
        font-weight: 800;
        font-size: 2rem;
        padding-bottom: 20px;
        color: #333;
    }

    h5 {
        line-height: 3;
        font-weight: 600;
        font-size: 1.1rem;
    }

    img {

        &:hover {
            opacity: 0.8;
            cursor: pointer;
        }
    }
}

footer {
    width: 100%;
    margin-top: 150px;
    border-top: 1px solid #eee;
    padding: 30px 0 0 0;

    .footer_inner {
        width: 70%;
        margin: 30px auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        cursor: default;

        img {
            width: 4rem;
            margin-top: 8px;
        }

        .info {
            color: #333;

            ul {

                &:first-child {
                    display: flex;

                    li {
                        padding-right: 10px;
                    }
                }
            }

            p {
                padding-left: 32px;
                font-size: 0.8rem;
            }

            li {
                font-size: 0.8rem;
            }
        }

        .center {

            color: #333;

            h6 {
                font-weight: 600;
                padding-left: 32px;
            }

            h1 {
                font-weight: 600;
                padding-left: 32px;
            }

            ul {
                li {
                    font-size: 0.9rem;
                }
            }
        }

        .notice {

            div {
                padding-left: 32px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                h6 {
                    font-weight: 600;
                }

                button {
                    border-radius: 0;
                    font-size: 0.5rem;
                    line-height: 0;
                    padding: 8px 8px;
                }

            }

            ul {

                li {
                    font-size: 0.85rem;
                    border-bottom: 1px solid #dfdfdf;
                    padding: 5px 0;
                    cursor: pointer;
                }
            }
        }
    }
}

// 카트 페이지
.cart_wrap {
    width: 70%;
    margin: 200px auto;

    h1 {
        text-align: center;
        margin-bottom: 100px;
        font-size: 2.2rem;
        font-weight: 600;
    }

    .cart_table {
        text-align: center;
        

        tbody {
            font-weight: 600;

            tr {

                td {
                    box-shadow: 0 0 0 #fff;

                    &:nth-child(2) {
                        text-align: left;
                    }

                    &.num {

                        button {
                            text-align: center;
                            padding: 2px;
                            border-radius: 0;
                            background-color: #00437b;
                            color: #fff;
                            font-weight: 600;
                            
                            &:hover {
                                background-color: #07599c;
                            }
                        }

                        span {
                            margin: 0 15px;
                            color: #333;
                            font-weight: 600;
                        }
                    }

                    &.cart_del {

                        button {
                            text-align: center;
                            padding: 2px 8px;
                            border-radius: 0;
                            background-color:#00437b;
                            color: #fff;
                            font-weight: 600;
                            font-size: 1rem;
                            border-color: #00437b;

                            &:hover {
                                background-color: #07599c;
                            }
                        }
                    }
                }
            }
        }
    }
    .cart_price {
        margin: 50px auto;

        h2 {
            text-align: center;
            font-size: 1.5rem;
            font-weight: 600;

            span {
                font-size: 1.7rem;
                padding: 3px;
            }
        }
        >div {
            text-align: center;

            button {
                padding: 10px 20px;
                border-radius: 0px;
                margin: 10px auto;
                background-color: #00437b;

                &:hover {
                    background-color: #07599c;
                }
            }
        }
    }
}



// 디테일 페이지
.detail_wrap {
    width: 70%;
    margin: 200px auto;

    .row {
        justify-content: center;


        .col-lg-5 {

            .table-striped {
                width: 100%;
                margin: 0 auto;

                thead {

                    th {

                        p {
                            &:first-child {
                                font-size: 1.2rem;
                                margin-bottom: 0;
                            }

                            &:last-child {
                                color: #e0002a;
                                font-size: 1.3rem;
                            }
                        }
                    }
                }

                tbody {
                    border-top: 2px solid #333;
                    border-bottom: 2px solid #333;

                    tr {

                        &:first-child {
                            border-bottom: 1px solid #333;
                        }

                        &:not(:first-child) {
                            border-bottom: 1px solid #fff;
                        }

                        td {
                            box-shadow: 0 0 0 #fff;
                            padding: 16px 8px;
                            font-size: 0.9rem;

                            >span {
                                color: #e0002a;
                                font-weight: 600;
                            }

                            &.img_td {
                                p {
                                    font-size: 0.9rem;
                                    margin-bottom: 0;
                                }

                                img {
                                    width: 40%;
                                }

                                button {
                                    display: inline-block;
                                    vertical-align: middle;
                                    border-radius: 0px;
                                    padding: 2px;
                                    font-size: 0.75rem;
                                    margin-left: 10px;

                                }
                            }

                            &:first-child {
                                width: 30%;
                                font-weight: 600;
                            }

                            &.num {

                                button {
                                    text-align: center;
                                    padding: 2px;
                                    border-radius: 0;
                                    background-color: #00437b;
                                    color: #fff;
                                    font-weight: 600;

                                    &:hover {
                                        background-color: #07599c;
                                    }
                                }

                                span {
                                    margin: 0 15px;
                                    color: #333;
                                }
                            }
                        }
                    }
                }
            }

            .detail_btn_wrap {
                text-align: center;

                button {
                    padding: 15px 60px;
                    margin: 20px 30px;
                    border: none;
                    font-weight: 600;
                    border-radius: 0;

                    &:last-child {
                        background-color: #e0002a;

                        &:hover {
                            background-color: #be092a;
                        }

                    }
                }
            }
        }
    }

    .nav-tabs {
        width: 100%;
        margin: 100px auto;

        li {
            margin: 0 auto;
            width: 25%;

            button {
                border-radius: 0px;
                color: #333;
                width: 100%;

                &.active {
                    font-weight: 600;
                    color: #333;
                }
            }
        }
    }

    .tab-content {
        text-align: center;
        margin-top: 150px;

        .exchange {
            width: 85%;
            margin: 0 auto;
            text-align: left;

            tbody {

                tr {

                    &:first-child {
                        border-top: 2px solid #333;
                    }

                    &:last-child {
                        border-bottom: 2px solid #333;
                    }

                    td {
                        box-shadow: 0 0 0 #fff;
                        font-size: 0.9rem;
                        padding: 30px 10px;


                        &:first-child {
                            background-color: #eee;
                        }

                        ul {
                            margin: 0;
                            padding: 0;

                            li {
                                font-size: 0.9rem;
                            }
                        }
                    }
                }
            }
        }

        .qna,
        .review {
            width: 85%;
            margin: 0 auto;
            text-align: left;

            table {
                width: 100%;
                margin-bottom: 30px;

                thead {
                    background-color: #00437b;
                    color: #fff;

                    th {
                        padding: 10px 10px;
                    }
                }

                tbody {

                    tr {
                        &:hover {
                            background-color: #eee;
                        }

                        td {
                            padding: 30px 10px;
                            box-shadow: 0 0 0 #fff;
                            cursor: pointer;

                            &.star {
                                color: #00437b;
                                font-size: 1.1rem;
                            }
                        }
                    }

                }

                tr {

                    &:first-child {
                        border-top: 2px solid #333;
                    }

                    &:last-child {
                        border-bottom: 2px solid #333;
                    }

                }
            }

            .pagination {
                justify-content: center;

                li {

                    &.active {
                        font-weight: 600;
                        cursor: pointer;

                        span {
                            background-color: #00437b;
                            border: 1px solid #00437b;
                        }
                    }



                    a {
                        color: #333;
                        font-weight: 600;

                        &:focus {
                            box-shadow: 0 0 0 #fff;
                        }
                    }
                }
            }

            h1 {
                text-align: center;
                font-size: 1.7rem;
                font-weight: 600;
                line-height: 3;
            }

            .card-group {
                margin-bottom: 100px;

                .card {
                    border-radius: 0px;
                    cursor: pointer;

                    &:hover img {
                        opacity: 0.8;
                    }

                    img {
                        border-radius: 0px;
                    }

                    .card-title {
                        color: #00437b;
                    }

                    .card-footer {
                        display: flex;
                        justify-content: space-between;
                    }
                }
            }
        }
    }
}

//로그인 페이지
.login_wrap {
    margin: 200px auto;
    text-align: center;

    >div {
        display: flex;
        width: 400px;
        flex-direction: column;
        align-items: center;
        border: 1px solid #e1e1e1;
        margin: 0 auto;
        padding: 20px 30px;
        

        &:nth-child(2) {
            border-top: none;
        }

        input {
            width: 380px;
            border: none;
            outline: none;
        }
    }

    button {
       width: 400px;
       margin: 30px auto 5px;
       background-color: #00437b;
       padding: 10px 30px;

       &:hover {
        background-color: #07599c;
       }
    }

    span {
        display: block;
        cursor: pointer;
        font-size: 0.9rem;
    }
}
